
.container {
    max-width: 960px;
    margin-right: auto;
    margin-left: auto;
    padding-right: 15px;
    padding-left: 15px;
}


.note-item {
    display: grid;
    grid-gap: 1rem;
    /* to make the columns changes */
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}